import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Plunder } from './plunder';
import { PlunderResult } from './plunder-result';

@Injectable()
export class PlunderService {

	plunderResults: Array<PlunderResult> = [];

	constructor(private http:Http) { }

	calcPlunder(p : Plunder) : Promise<Array<PlunderResult>> {
		return this.http.post('/plunder-calculator', p).toPromise()
			.then(r => {
				var arr = r.json();
				this.plunderResults = [];
				for (var i = 0; i < arr.length; i++) {
					this.plunderResults.push(new PlunderResult(arr[i]));
				}
				return this.plunderResults;
			})
	}
}
