export class SpecialAntExp {
    id: number;
    xp:number;
    xp_total:number;
    
	constructor(id:(number|any), xp?: number, xp_total?: number) {
        if (typeof id == "number") {
            this.id = id;
            this.xp = xp;
            this.xp_total = xp_total;
        }
        else {
            this.id = parseInt(id['id']);
            this.xp = id['xp'];
            this.xp_total = id['xp_total'];
        }
    }
    
    static generate() {
        return new SpecialAntExp(0, 0, 0);
    }
    
    clone() {
        return new SpecialAntExp(this.id, this.xp, this.xp_total);
    }
}